* {
  box-sizing: border-box;
}

html {
  width: 100%;
  min-height: 100%;
}

.login-welcome {
  color: #c0c0c0;
  /* margin: 8% 0 3% 0; */
  /* padding: 1%; */
  text-align: center;
}

.login-form {
  /* border: 5px solid red; */
  border-top: 20px solid #002357;
  border-radius: 5%;
  background-color: #c0c0c0;
  box-shadow: 0px 15px 150px 10px #c0c0c0;
  overflow-y: scroll;
  /* height: 300px; */
  /* padding-top: 10%; */
}

/* div.container {
  padding: 0;
} */

.login-form-group {
  width: 100%;
  margin-bottom: 3%;
  /* border: 5px solid white; */
}

.login-form-control {
  border: 5px solid #002357;
  /* height: 2.5em; */
  border-radius: 5%;
  width: 100%;
  padding-left: 2%;
}

:placeholder-shown {
  font-weight: 800;
}

.btn-submit-login {
  background-image: radial-gradient(#eee243 15%, #aa8b00 90%);
  font-weight: 800;
  /* font-size: 1.5em; */
  /* padding: 3%; */
  /* width: 50%; */
  border-radius: 10%;
  /* margin-top: 2%; */
  border: 1px solid #002357;
  box-shadow: 5px 10px 10px #002357;
}

.btn-submit-login:active {
  color: #c0c0c0;
  box-shadow: 5px 10px 10px #0000c2;
}

/* **********  MEDIA QUERIES  ******** */

@media only screen and (max-width: 600px) {
  .login-welcome {
    margin: 5% 0 10% 0;
  }

  .login-form {
    padding-top: 8%;
    height: 250px;
  }

  .login-form-control {
    height: 2.5em;
  }

  .btn-submit-login {
    padding: 2%;
    width: 30%;
  }
}

/* Small devices (landscape phones and large portrait phones 600px and up) */
@media only screen and (min-width: 600px) {
  .login-welcome {
    margin: 2% 0 2% 0;
  }

  .login-form {
    margin: 0 0 15% 0;
    padding-top: 5%;
    height: 260px;
  }

  .login-form-control {
    height: 2.5em;
  }

  .btn-submit-login {
    padding: 2%;
    width: 25%;
  }
}

/* Medium devices (Portrait tablets and large landscape phones) */
@media only screen and (min-width: 768px) {
  .login-welcome {
    padding: 4% 5% 4% 5%;
  }

  .login-welcome h2 {
    font-size: 2.5em;
  }

  .login-form {
    margin: 5% 5% 15% 5%;
    border-top: 40px solid #002357;
    height: 325px;
    border-radius: 5%;
  }

  .login-form-control {
    height: 2.75em;
  }

  .btn-submit-login {
    /* margin-top: 2%; */
    font-size: 1.25em;
  }

  .footer img {
    height: 100px;
    width: 450px;
  }
}

/* Large handhelds (tablets landscape, large tablets portrait) */
@media only screen and (min-width: 992px) {
  .login-welcome {
    font-size: 1.25em;
    padding: 0;
    margin: 2% 0 5% 0;
  }

  .login-form {
    padding-top: 5%;
    margin: auto;
    margin-top: 10%;
    width: 80%;
    height: 420px;
  }

  .login-form-control {
    width: 90%;
    height: 3.75em;
  }

  .alert.alert-danger.p-1 {
    width: 90%;
    margin: auto;
    font-size: 1.5em;
  }

  .btn-submit-login {
    font-size: 1.5em;
    margin-top: 2%;
    width: 30%;
  }

  :placeholder-shown {
    font-weight: 800;
  }

}

/* Large devices (small laptops and desktops) */
@media only screen and (min-width: 1200px) {
  .login-welcome {
    font-size: 2em;
  }

  .login-form-control {
    height: 3em;
  }

  .btn-submit-login {
    margin-top: 0;
  }
}

/* Large devices (tablets landscape, large tablets portrait) */
@media only screen and (min-width: 1500px) {
  .login-welcome {
    padding: 0;
    margin-bottom: 2%;
  }

  .login-welcome h2 {
    font-size: 1.75em;
  }

  .login-form {
    padding-top: 2%;
    width: 60%;
    margin: 0 20% 0 20%;
    height: 300px;
  }

  .login-form-control {
    height: 2.5em;
  }

  .btn-submit-login {
    /* margin-top: 4%; */
    font-size: 1em;
    padding: 1%;
    width: 25%;
  }

  footer p {
    font-size: 1.5em;
  }
}

