body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

html {
  width: 100%;
  min-height: 100%;
}

.signup-welcome {
  color: #c0c0c0;
  padding: 1%;
  margin: 8% 0 8% 0;
  text-align: center;
}

.signup-form {
  border-top: 20px solid #002357;
  border-radius: 5%;
  background-color: #c0c0c0;
  overflow-y: scroll;
  box-shadow: 0px 0px 50px 5px #aa8b00;
  box-shadow: 0px 0px 50px 5px #C0C0C0;
  height: 350px;
  padding-top: 12%;
}

.signup-form-group {
  width: 100%;
  margin-bottom: 3%;
}

.signup-form-control {
  border: 5px solid #002357;
  width: 100%;
  height: 2.75em;
  border-radius: 5%;
  padding-left: 2%;
}

.btn-submit-signup {
  background-image: radial-gradient(#eee243 15%, #aa8b00 90%);
  font-weight: 800;
  padding: 3%;
  width: 50%;
  border-radius: 10%;
  margin-top: 2%;
  border: 1px solid #002357;
  box-shadow: 5px 10px 10px #002357;
}

.btn-submit-signup:active {
  color:#C0C0C0;
  box-shadow: 5px 10px 10px #0000c2;
}

/* **********  MEDIA QUERIES  ******** */
/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) { 
  .signup-welcome {
    padding: 0;
    margin: 5% 0 7% 0;
  }

  .signup-form {
    height: 340px;
    padding-top: 8%;
  } 

  .signup-form-control {
    height: 2.5em;
  }

  .btn-submit-signup {
    font-size: 1.15em;
    margin-top: 5%;
    padding: 3%;
    width: 40%
  }

}

/* Small devices (landscape phones and large portrait phones 600px and up) */
@media only screen and (min-width: 600px) {
  .signup-welcome {
    padding: 0;
    margin: 2% 0 2% 0;
  }
  .signup-form {
    padding-top: 4%;
    height: 300px;
  }

  .signup-form-group {
    margin-bottom: 1%;
  }

  .signup-form-control {
    height: 2.5em;
  }

  .btn-submit-signup {
    font-size: 1em;
    width: 30%;
    padding: 2%;
  }

  footer p {
    font-size: 1.5em;
    padding: 1%;
  }
} 

/* Medium devices (Portrait tablets and large landscape phones) */
@media only screen and (min-width: 768px) {
  .signup-welcome {
    font-size: 2.5em;
    padding: 4% 5% 4% 5%;
  }

  .signup-form {
    margin: 5% 5%;
    padding-top: 5%;
    border-top: 40px solid #002357;
    height: 425px;
    width: 90%;
    border-radius: 5%;
    box-shadow: 0px 0px 100px 15px #C0C0C0;
  }

  .signup-form-control {
    height: 3em;
  }

  .btn-submit-signup {
    font-size: 1.5em;
    font-weight: 800;
    margin-top: 4%;
  }

  .footer img {
    height: 100px;
    width: 450px;
  }
}

/* Large handhelds (tablets landscape, large tablets portrait) */
@media only screen and (min-width: 992px) {
  .signup-welcome {
    font-size: 3.5em;
    padding: 5% 5% 0 5%;
  }

  .signup-form {
    padding-top: 8%;
    border-top: 60px solid #002357;
    height: 600px;
  }

  .signup-form-control {
    height: 3.5em;
  }

  .alert.alert-danger.p-1 {
    width: 90%;
    margin: auto;
    font-size: 1.5em;
  }

  .btn-submit-signup {
    font-size: 2em;
    font-weight: 800;
    margin-top: 5%;
    padding: 2%;
  }
}


/* Large devices (small laptops and desktops) */
@media only screen and (min-width: 1200px) {
  .signup-welcome {
    font-size: 3em;
    margin: 2% 0 5% 0;
    padding: 0;
  }

  .signup-form {
    padding-top: 2%;
    border-top: 60px solid #002357;
    height: 400px;
    width: 60%;
    margin: 0 0 10% 20%;
  }

  .signup-form-control {
    height: 2.5em;
    width: 80%;
  }

  .btn-submit-signup {
    font-size: 1.25em;
    font-weight: 800;
    margin-top: 4%;
    padding: 2%;
  }

}

/* Large devices (tablets landscape, large tablets portrait) */
@media only screen and (min-width: 1500px) {
  .signup-welcome {
    margin-bottom: 3%;
  }

  .signup-form {
    width: 50%;
    margin: auto;
  }

  .btn-submit-signup {
    font-weight: 800;
    margin-top: 2%;
  }

}


